.popup {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  z-index: 99;
  background: rgba(0, 0, 0, 0.8);
  display: none;
  &.show {
    display: block;
  }
  .content {
    width: 14.4rem;
    height: 7.5rem;
    background: #fefbf6 url("../assets/img/kv/bg-sky.png") no-repeat;
    // background-size: 13.88rem 5.03rem;
    background-size: 13.3rem 6.6rem;
    background-position: center center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    .bird {
      width: 3.77rem;
      height: 4.12rem;
      background: url("../assets/img/kv/bird.png");
      background-size: cover;
      position: absolute;
      transform: scale(0.6) rotate(-60deg);
      top: -0.9rem;
      right: 0rem;
    }
    .tree-flower {
      width: 1.01rem;
      height: 3.56rem;
      background: url("../assets/img/material/tree+flower1.png");
      background-size: cover;
      position: absolute;
      top: 3.8rem;
      left: 0;
    }
    .butterfly {
      width: 0.93rem;
      height: 0.96rem;
      background: url("../assets/img/kv/butterfly2.gif");
