      left: 1.5rem;
      border-radius: 0.1rem;
      .swiper-slide {
        // width: 6rem; // width: 11.5rem;
        // height: 3.2rem;
        // border: 1px solid red;
      }
    }
    .progress {
      position: absolute;
      top: 5.7rem;
      left: 1.5rem;
      font-size: 0.3rem;
      text-align: left;
      ::v-deep {
        span {
          font-size: 0.22rem;
        }
        p {
          font-size: 0.18rem;
          display: inline-block;
        }
      }
    }
    .all,
    .video {
      width: 0.8rem;
      height: 0.3rem;
      color: white;
      line-height: 0.3rem;
      font-size: 0.16rem;
      border-radius: 0.21rem;
      background: #afcdcd;
      position: absolute;
      top: 5.7rem;
      left: 5.85rem;
      cursor: pointer;
      pointer-events: visible;
      &.active {
        background: #3b8784;
        pointer-events: none;
      }
    }
    .video {
      left: 6.75rem;
    }
    .sub-title {
      height: 0.2rem;
      line-height: 0.2rem;
      position: absolute;
      top: 1.7rem;
      left: 8.1rem;
      font-size: 0.16rem;
      text-align: left;
      font-weight: 600;
      color: #086d6a;
    }
    .main-title {
      // width: 2rem;
      position: absolute;
      top: 2rem;
      left: 8.1rem;
      font-size: 0.34rem;
      font-weight: 800;
      text-align: left;
      color: #086d6a;
      span {
        font-size: 0.18rem;
        font-weight: 500;
      }
    }
    .desc {
      width: 4.8rem;
      max-height: 2.75rem;
      position: absolute;
      top: 2.65rem;
      left: 8.1rem;
      text-align: left;
      // background: red;
      font-size: 0.16rem;
      line-height: 1.55;
      overflow: auto;
      word-break: break-all;
      padding-right: 0.1rem;
    }

    .prevBtn,
    .nextBtn {
      width: 0.8rem;
      height: 0.8rem;
      line-height: 0.8rem;
      border-radius: 50%;
      border: none;
      color: #fff;
      background: rgba(94, 157, 154, 0.7);
      cursor: pointer;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      &:before,
      &:after {
        content: "";
        position: absolute;
        height: 0.03rem;
        width: 0.15rem;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        background: #fff;
        border-radius: 0.05rem;
      }
      &:before {
        top: 0.09rem;
        transform: rotate(45deg);
      }
      &:after {
        top: -0.09rem;
        transform: rotate(-45deg);
      }
    }
    .prevBtn {
      left: 0.3rem;
    }
    .nextBtn {
      right: 0.44rem;
      &:before {
        transform: rotate(-45deg);
      }
      &:after {
        transform: rotate(45deg);
      }
    }
    .close {
      width: 0.5rem;
      height: 0.5rem;
      position: absolute;
      display: inline-block;
      top: 0.15rem;
      right: 0.15rem;
      cursor: pointer;
      &:before,
      &:after {
